<template>
   <div class="shop-content-wrap">
		<template v-if="gridListView==true && listData != null">
		</template>
		<template v-else>
			<currency-list-view :data="listData" @deleteProduct="itemDeleted"></currency-list-view>			
		</template>

   </div>   
</template>

<script>
import currencyListView from "./currencyListView.vue";
import moment from 'moment'
import Currency from "Api/currency";
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["linksformbackend"])
  },
  props: ["colxs", "colsm", "colmd", "collg", "colxl", "gridListView"],
  data() {
    return {
      products: null,
      listData: []
    };
  },
  async created() {
    try {
      const res = await Currency.getcurrency()
      // console.log(res)
      res.data.data.forEach(el => {
        // console.log(el)
        const dat = moment(el.updatedAt)
			const time = moment(el.updatedAt)
			const tt = time.format('LT')
			const da = dat.format('L');
			const date = tt + '' + ' '+ da
        this.listData.push({
          id: el._id,
          symbol: el.symbol,
          name: el.name,
          price: 'RWF'+el.currentValue,
          date: date
        })
      });
    } catch (err) {
      console.log(err)
    }
  },
  methods: {
    itemDeleted(item) {
      let deletedToDoList = this.listData;
      let index = deletedToDoList.indexOf(item);
      this.listData.splice(index, 1);
    },
  },
  components: {
    currencyListView
  }
};
</script>
